import React, { useState } from 'react'
import Nav from '../templates/header'
import Cookies from 'universal-cookie'

import axios from '../API.js'
import { validateEmail, clear, validateFields, customValidation } from '../common.js'
const cookies = new Cookies();

class Work extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			id: '1', email: '', requesterid: '', reward: '0', filename: '', filepath: '', entertext: '', isError: false,
			error: { entertext: '' },
			successMsg: '', imgWidth: null
		}
	}
	generateImage = (data) => {
		let random = Math.floor(Math.random() * 51);
		data.filepath = "http://freelancetypers.com/images/works/" + random + ".jpg"
		data.filename = random + ".jpg"
		data.requesterid = this.props.location.state.id
		data.reward = this.props.location.state.reward
		data.email = cookies.get('email')

		this.setState({ data })
	}
	componentDidMount() {
		if (this.props.location.state !== undefined) {
			const data = this.state
			this.generateImage(data)
			axios.get("/userWorks", { params: { email: data.email, filepath: data.filepath } }).then(res => {
				if (res.data.data.length > 0) {
					this.generateImage(data)
				}
			})
		}
	}

	zoomin = () => {
		var myImg = document.getElementById("map");
		var currWidth = myImg.clientWidth;
		const data = this.state
		data.imgWidth = data.imgWidth === null ? currWidth : data.imgWidth
		if (currWidth == 2500) return false;
		else data.imgWidth = (data.imgWidth + 100)

		this.setState({ data })
	}

	zoomout = () => {
		var myImg = document.getElementById("map");
		var currWidth = myImg.clientWidth;
		const data = this.state
		data.imgWidth = data.imgWidth === null ? currWidth : data.imgWidth
		if (currWidth == 100) return false;
		else data.imgWidth = (data.imgWidth - 100)
		this.setState({ data })
	}
	onChange = (e) => {
		const data = this.state
		data[e.target.name] = e.target.value
		this.setState({ data })
		this.onValidation(data)
	}
	onValidation = (inp) => {
		const emptyFields = [
			{ key: 'entertext', errVal: 'Enter text properly' }
		]
		const data = validateFields(inp !== undefined ? inp : this.state, emptyFields)
		this.setState({ data })
		return data.isError
	}
	onSubmit = () => {
		const data = this.state
		data.userid = 1
		if (!this.onValidation()) {
			console.log('hii')
			axios.post("/userWorks/Add", data).then(res => {
				console.log(res)
				if (res.data.status === 'success')
					setTimeout(() => {
						this.props.history.push({ pathname: '/allhits' })
					}, 3000)
				this.setState({ successMsg: 'Your Work submitted successfully!' })
			}).catch(err => {
				console.log(err)
			})
		}
	}
	render() {
		// let random = Math.floor(Math.random() * 51);
		// let imgPath = "http://freelancetypers.com/images/works/" + random + ".jpg"
		// let imgName = random + ".jpg"

		let res = (<>
			<Nav mainactive="hits" subactive="allhits" />

			<div className="p-4">

				<div className="row">
					<div className="col-6">
						<div className="mb-2">
							<button type="button" className="button-sm" onClick={this.zoomin}> Zoom In</button>{'  '}
							<button type="button" className="button-sm" onClick={this.zoomout}> Zoom Out</button>
						</div>
						<div class="container-outer">
							<img id="map" src={this.state.filepath} width={this.state.imgWidth === null ? "100%" : this.state.imgWidth} />
						</div>
					</div>

					<div className="col-6">
						<h2>Extract purchased items from a shopping receipt</h2>
						<h5 className="mb-4">Hit Reward</h5>

						<textarea name="entertext" className="col-12" placeholder="Please type here" style={{ height: '10em' }} onChange={this.onChange} value={this.state.entertext}></textarea>
						{this.state.error.entertext}
						{this.state.successMsg !== '' ? <div className="success"><i class="fa fa-check" aria-hidden="true"></i> {this.state.successMsg}</div> : ''}

						<div className="text-right">
							<button type="button" className="button-sm" onClick={this.onSubmit}> Submit</button>{'  '}
							<button type="button" className="button-sm" onClick={() => { this.setState({ entertext: '' }) }}> Cancel</button>
						</div>

					</div>

				</div>

			</div>
		</>)

		return res
	}
}

export default Work;
